import React from "react";
import { Filter } from "./Filter";
import faker from "faker";

const n = 21;

export const List = () => (
    <div className="container">
        <div className="d-flex p-4">
            <Filter />
        </div>
        <table className="table">
            <thead>
                <tr>
                    <th scope="col">#</th>
                    <th scope="col">Institution name</th>
                    <th scope="col">City, Country (HQ)</th>
                    <th scope="col">Institution type</th>
                    <th scope="col">AUM</th>
                </tr>
            </thead>
            <tbody>
                {[...Array(n)].map((e, i) => <Row key={i} index={i} />)}
            </tbody>
        </table>
    </div>
);

const Row = ({ index }) => (
    <tr>
        <th scope="row">{index + 1}.</th>
        <td>{faker.company.companyName()}</td>
        <td>{faker.address.city()}, {faker.address.country()}</td>
        <td>{faker.name.jobTitle()}</td>
        <td>{faker.finance.amount()}</td>
    </tr>
);